import { defineComponent, ref, onDeactivated } from '@nuxtjs/composition-api';
const defaultConfigBtn = {
    icon: true,
    color: 'primary'
};
export default defineComponent({
    props: {
        hasConfig: {
            type: Boolean,
            default: false
        },
        btnConfigStyle: {
            type: Object,
            default: () => defaultConfigBtn
        }
    },
    setup(_, { emit }) {
        let interval = null;
        let countRefresh = 1;
        const times = ref(0);
        const timeUnit = ref('minus');
        const textError = ref('');
        const status = ref('just now');
        const onStart = () => {
            if (timeUnit.value === 'second') {
                if (times.value < 5) {
                    textError.value = 'min value is 5 seconds.';
                    return;
                }
            }
            if (timeUnit.value === 'minus') {
                if (times.value < 1) {
                    textError.value = 'min value is 1 minus.';
                    return;
                }
            }
            status.value = 'just now';
            textError.value = '';
            let second = times.value * 1000;
            if (timeUnit.value === 'minus')
                second *= 60;
            interval = setInterval(() => {
                const diffTime = Math.round((second * countRefresh) / 1000);
                if (diffTime < 60)
                    status.value = `update ${diffTime}s ago`;
                else {
                    const minus = Math.round(diffTime / 60);
                    if (minus < 60)
                        status.value = `update ${minus}m ago`;
                    else
                        status.value = `update ${Math.round(minus / 60)}h ago`;
                }
                emit('refresh', countRefresh++);
            }, second);
        };
        const onStop = () => {
            if (!interval)
                return;
            status.value = 'refresh has stoped.';
            clearInterval(interval);
        };
        onDeactivated(() => onStop());
        return { times, timeUnit, textError, status, onStart, onStop };
    }
});
